import Vue from 'vue'
import Router from 'vue-router'
import Photo from '@/components/Photo'
import Content from '@/components/Content'
import index from '@/components/index'

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      redirect: '/index'
    },
    {
      path: '/index',
      name: 'index',
      components: {
        photo: Photo,
        content: Content
      }
    },
    {
      path: '/note',
      name: 'note',
      components: {
        index: index
      }
    }
    // {
    //   path: '/about',
    //   name: 'about',
    //   components: {
    //     header:Header,
    //     home:About
    //   }
    // }
  ]
})



